$input-radio-inner-size: 16px;
$input-radio-inner-bullet-size: 10px;
$input-radio-inner-margin: 6px;

$ctrl-focus-shadow: 0 0 1px 2px rgba(82, 168, 236, .5);

.input-radio {
  label.radio {
    display: flex;
    flex-flow: row wrap;
  }

  input[type=radio][xname] {
    outline: 0;

    &:hover {
      & + .input-radio-inner {
        border-color: $grey-500;
      }
    }

    &:focus {
      & + .input-radio-inner {
        box-shadow: $ctrl-focus-shadow;
      }
    }

    &:checked {
      & + .input-radio-inner {
        border-color: $blue;
      }

      & + .input-radio-inner:after {
        opacity: 1;
        transform: scale(1);
      }
    }
  }
}

.input-radio-inner {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: $input-radio-inner-size;
  height: $input-radio-inner-size;
  margin-right: $input-radio-inner-margin;
  margin-left: -22px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 50%;

  &:after {
    content: '';
    display: block;
    width: $input-radio-inner-bullet-size;
    height: $input-radio-inner-bullet-size;
    background-color: $blue;
    border-radius: 50%;
    opacity: 0;
    transform: scale(0);
    transition: all .2s ease-in-out;
  }
}

.ctrl-option-help {
  width: 100%;
  font-size: 11px;
  color: $grey-500;
}

[data-ctrl-help] {
  display: none;
}

// Input Radio: positioning fix
.radio {
  display: flex;
  align-items: center;

  input[type=radio] {
    margin-right: 8px;
  }
}
