.empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: $space-m;
}

.empty-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #f9f9f9;
  font-size: 32px;
  color: $grey-500;
  box-shadow: inset 0 4px 12px 0 rgba(0, 0, 0, .05),
              inset 0 2px 3px 0 rgba(0, 0, 0, .025);
}

.empty-content {
  text-align: center;
}

.empty-title {
  font-size: $size-title;
  font-weight: 700;
  color: $grey-800;
}

.empty-message {
  max-width: 374px;
  margin: 0 auto;
  color: $grey-600;
  font-size: 13px;
}
