$form-secion-border: 1px solid $grey-200;
$form-help-height: 24px;
$form-ctrl-height: 30px;
$form-group-gap: 28px;
$form-text-gap: 18px;

//
// Form Section
// to add border and space separation between sections
// prefer to use with <section> tag.
//
.form-section {
  position: relative;
  padding-bottom: $space-m;
  font-size: $size-text-base;
  overflow-x: hidden;

  &:not(:last-of-type) {
    border-bottom: $form-secion-border;
  }

  &.-gapless {
    padding-bottom: 0;
  }

  &.-space {
    padding-top: $space-m;
  }
}

//
// Form Container
// to add form vertical margin/space
//
.form-container {
  padding: 0 $space-l 0 $space-l;
}

//
// Form Title
//
.form-title {
  margin-bottom: $space-m;
  margin-top: $space-xs;
  font-size: $size-title;
  font-weight: 700;
  color: $grey-900;

  > h1 {
    display: inline-block;
    margin-bottom: 0;
    line-height: 2;
    font-size: inherit;
    font-weight: inherit;
    color: inherit;
  }

  > p {
    font-size: 12px;
    font-weight: 400;
    color: $grey-600;
  }
}

.form-title-icon {
  display: inline-block;
  margin-right: $space-xs;
  font-size: $size-title-icon;
  color: $blue;
}

.form-title-tagline {
  display: block;
  margin-bottom: $space-xxs;
  font-weight: normal;
  font-size: $size-title-tagline;
  color: $grey-600;

  &.-double {
    min-height: 40px;
  }
}

.form-group {
  position: relative;

  &.-margin {
    margin-bottom: $form-group-gap;
  }

  &.-space {
    margin-top: $space-m;
  }

  &.-error {
    .form-input {
      .input-group-addon {
        border-color: $color-error;
      }

      input,
      select,
      textarea {
        border-color: $color-error;

        &:focus {
          // override default bootstrap :focus border-color and box-shadow
          border-color: $color-error-focus !important;
          box-shadow: inset 0 1px 1px rgba(0,0,0,.075),
                      0 0 8px $color-error-focus-shadow !important;
        }
      }
    }

    .form-help {
      position: absolute;
      color: $color-error;
    }
  }
}

// Form Group Horizontal
.form-group.-horizontal {
  display: flex;

  > .form-label {
    flex-basis: 0;
    flex-grow: 1;
    flex-shrink: 0;

    & + .form-text,
    & + .form-input {
      margin-left: 12px;
    }
  }

  > .form-text,
  > .form-input {
    flex-basis: 0;
    flex-grow: 2;
    flex-shrink: 1;
  }
}

//
// Form Label
//
.form-label {
  margin-bottom: 3px;
  white-space: nowrap;
  font-size: $size-text-base !important; // override global.css label style
  font-weight: 500;
  color: $grey-800;
  cursor: default; // override bootstrap label style

  &.-bold {
    font-weight: 700;
  }

  &:empty {
    display: none;

    & + .form-help {
      display: none;
    }
  }

  &[data-help] {
    &:after {
      content: attr(data-help);
      font-size: $size-text-small;
      font-weight: 400;
      color: $grey-500;
      margin-left: 4px;
    }
  }
}

//
// Form Text Values (Readonly inputs)
//
.form-text {
  position: relative;
  display: block;
  margin-bottom: 0;
  color: $grey-600;

  &:last-of-type {
    margin-bottom: $form-text-gap;
  }

  &.-gapless {
    margin-bottom: 0;
  }
}

//
// Form Inputs
//
.form-input {
  position: relative;
  line-height: $form-ctrl-height;
  // min-height: $form-ctrl-height;
  color: $grey-600;

  &:empty {
    display: none;
  }

  // apply margin directly to inputs controls
  // avoiding ghost margins when Orquestra
  // FormFields aren't visible
  &:not(.-gapless) {
    input[type=text],
    textarea,
    select,
    div[xid],
    .btn-group,
    .radio:last-of-type,
    .checkbox:last-of-type,
    > .btn {
      margin-bottom: $form-group-gap;
    }
  }

  &.-gapless {
    > * {
      margin-bottom: 0;
    }
  }


  // enable readonly fields margin
  div[xid] {
    display: inline-block !important;
    line-height: 30px;
  }

  // date modifier to limit input size
  // accordinly datepicker size for: DD/MM/YYYY
  &.-date {
    max-width: 114px;

    > input[type=text] {
      width: 100% !important;
    }
  }

  &.-md {
    max-width: 124px;

    > input[type=text],
    > textarea {
      max-width: 304px;
      width: 100% !important;
    }
  }

  &.-lg {
    max-width: 484px;

    > input[type=text],
    > textarea {
      max-width: 484px;
      width: 100% !important;
    }
  }
}

//
// Icons for Text Inputs
//
.form-input {
  &.-icon-right {
    &:not(.input-group) {
      > input[type=text] {
        padding-right: 28px;
      }
    }
    .form-input-icon {
      right: 10px;
    }
  }

  &.-icon-left {
    &:not(.input-group) {
      > input[type=text] {
        padding-left: 28px;
      }
    }
    .form-input-icon {
      left: 10px;
    }
  }

  // Hide icons when Orquestra FormField is readonly
  input[type=hidden] + .form-input-icon {
    display: none;
  }
}

.form-input-icon {
  position: absolute;
  line-height: $form-ctrl-height;
  color: $grey-600;
}

//
// Label Help Text
//
.form-help {
  z-index: 999;
  margin: 0;
  max-width: 480px;
  line-height: $form-help-height;
  font-size: $size-text-small;
  color: $grey-600;

  &.-long {
    margin-top: 3px;
    line-height: 1.6;
  }
}

//
// Table & Form
//
.form-input,
.form-table {
  input[type=text],
  select,
  textarea {
    box-sizing: border-box;
  }

  input[type=text],
  select {
    height: $form-ctrl-height;
  }
}

//
// Tables
//
.form-table {
  input[type=text],
  select,
  textarea {
    width: 100% !important;
    margin-bottom: 0; // override bootstrap style
  }
}
