$loader-size: 12px;
$loader-color: $grey-500;

@mixin loader($color: $loader-color) {
  content: '';
  position: absolute;
  display: block;
  left: calc(50% - (#{$loader-size}/2));
  top: calc(50% - (#{$loader-size}/2));
  height: $loader-size;
  width: $loader-size;
  border: 2px solid $color;
  border-radius: 50%;
  border-right-color: transparent;
  border-top-color: transparent;
  animation: spinAround .5s infinite linear;
}

@mixin pulseLoader($size: $loader-size) {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    display: block;
    width: $size;
    height: $size;
    border-radius: 50%;
    background-color: $blue;
    animation: spinBounce 1s 0s ease-in-out infinite;
  }
}

@mixin paneLoader {
  position: relative;
  min-height: 234px;
  background: radial-gradient(rgba(236, 241, 255, .5), transparent);
  margin-top: 0;

  * {
    display: none;
  }

  &:before {
    content: 'Carregando...';
    top: calc(50% + 12px);
    left: 0;
    width: 100%;
    text-align: center;
    position: absolute;
    font-size: $size-text-small;
    color: $color-link;
  }

  &:after {
    @include loader($color-link);
  }
}
