// Disclaimer: Nível de Especificidade
//
// utilizado o seletor `table.form-table` e
// `tbody tr td` para sobrescrever as propriedades
// default do Orquestra, incrementando a especificidade
// necessária.

$table-expand-trigger-width: 36px;

table.form-table {
  min-width: 100%;
  border: none;
  margin-bottom: 48px;

  tbody tr {
    border: none; // reset execute.css tr border style
  }

  tbody td {
    position: relative; // to enable .th-sup absolute positioning and absolute centering
  }

  tr.header.-white {
    background-color: #fff;

    td {
      border-top: none;
    }
  }

  tr.header td {
    line-height: 24px;
    vertical-align: middle;
    border-top: 1px solid $grey-300;
    border-bottom: 1px solid $grey-300;
  }

  tbody tr td {
    border: none;
    padding: 12px 10px;
    vertical-align: top;

    input,
    select,
    .form-input > div[xid] {
      margin-bottom: 0;
    }
  }

  &[mult=S].-static {
    td:first-child * {
      display: none;
    }
  }

  // adjust 'Excluir' button position
  .btn-delete-mv {
    position: relative;
    top: 3px;
  }

  &.-readonly {
    td {
      vertical-align: baseline;
    }

    td:first-child {
      padding-left: 74px + 10px;
    }

    .form-input-icon {
      display: none;
    }
  }

  &.-line-height {
    td {
      height: 30px;
    }
  }

  &.-hover {
    tr:not(.header):hover {
      background-color: $color-table-hover;
    }
    margin-bottom: 0;
  }

  &.-footer:not(.-hover):not(.-readonly) {
    margin-bottom: 3px;
  }
}

tfoot.form-table-footer {
  td {
    padding: 12px 10px;
    border-top: 1px solid $grey;
    border-bottom: 1px solid $grey;
    background-color: #f5f5f5;
    font-weight: 500;
    color: #303030;
  }
}

.th-sup {
  font-size: 9px;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  color: $color-disabled;
  font-weight: 400;
  position: absolute;
  top: -14px;
}

.form-table.-expand {
  tr {
    display: flex;
    flex-flow: row wrap;
    padding: 0;
    margin: 0;

    &.-open {
      .form-table-subrow {
        display: initial;
      }

      .form-table-expand-icon {
        transform: rotate(90deg);
      }
    }
  }

  &.-readonly {
    td:first-child {
      padding-left: 74px;
    }
  }

  td {
    flex: 1;
    border-bottom: 1px solid $grey-200;

    &:first-child {
      min-width: 74px;
      max-width: 74px;
    }

    &.form-table-subrow {
      display: none;
      padding-top: 24px;
      padding-bottom: 24px;
      padding-left: 74px + 10px + $table-expand-trigger-width;
      min-width: 100%;
      background-color: $grey-100;
      box-shadow: inset 0 1px 2px 1px rgba(0, 0, 0, .02);

      .form-label {
        margin-bottom: 14px;
      }
    }
  }

  &:not(.-readonly) {
    td:first-child {
      min-width: 74px;
      max-width: 74px;
      text-align: right;
    }
  }
}
.form-table-expand-trigger,
.form-table-expand-th {
  max-width: $table-expand-trigger-width;
  min-width: $table-expand-trigger-width;
}

.form-table-expand-trigger {
  text-align: center;
  font-size: 14px;
  color: $grey-600;
  cursor: pointer;

  &:hover {
    .form-table-expand-icon {
      opacity: .6;
    }
  }
}

.form-table-expand-icon {
  transition: transform linear .15s;
}
