.tag {
  font-size: 12px;
  background-color: #eaeaea;
  color: $grey-dark;
  font-weight: 500;
  padding: 4px 6px;
  border-radius: 4px;

  &.-success,
  &.-green {
    background-color: #e3f8eb;
    color: #317046;
  }

  &.-danger,
  &.-red {
    background-color: #fee5e4;
    color: #8b3e3a
  }

  &.-warning,
  &.-yellow {
    background-color: #fdfddc;
    color: #854d16;
  }

  &.-info,
  &.-blue {
    background-color: #ebf8ff;
    color: #2a4365;
  }
}
