#equipments-search {
  padding-bottom: 32px;
  border-bottom: 1px solid $grey-200;
}

#equipments-table {
  #BtnInsertNewRow {
    display: none;
  }

  td.-sm {
    max-width: 200px;
  }

  td.-md {
    min-width: 332px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .form-table-subrow {
    .columns {
      max-width: 1424px;
    }

    .column:nth-child(1) {
      .form-label {
        min-width: 160px;
        max-width: 184px;
      }
    }

    .column:nth-child(2) {
      .form-label {
        min-width: 136px;
        max-width: 160px;
      }
    }
  }
}

@media (max-width: 1274px) {
  #equipments-table {
    td.-md {
      min-width: initial;
    }
  }
}
