.input-group {
  position: relative;
  display: flex;

  em {
    display: none; // hide Orquestra em default element
  }

  > input:first-child {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }

  &.-center {
    > input {
      text-align: center;
    }
  }

  &.-small {
    > input {
      width: $input-size-small !important; // override Orquestra default width
    }
  }

  &.-icon-right {
    > input:not(:first-child) {
      border-radius: 0;
    }
  }

  .input-group-addon:last-child {
    color: $grey-dark-light;
    border-right: 1px solid #ccc;
    border-radius: 0 4px 4px 0;
  }

  &.-transparent {
    > input:first-child {
      border-right: 1px solid transparent;
    }

    > input:focus {
      border: 1px solid $color-input-border-focus;
      z-index: 1;
    }

    > input:last-child {
      border-left: 1px solid transparent;
    }

    .input-group-addon {
      background-color: #fff;
      color: $grey-dark;
      margin-left: -2px;
      margin-right: -2px;
      padding-left: 5px;
      padding-right: 5px;
      z-index: 0;
      box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.075);
      vertical-align: middle;

      &:last-child {
        padding-right: 6px;
        padding-left: 6px;
      }
    }
  }

  &.-readonly {
    align-items: center;

    > .input-group-addon {
      background-color: transparent;
      border: none !important;
      box-shadow: none;
      line-height: 22px;

      &:last-child {
        display: none;
      }
    }

    .fas {
      display: none;
    }
  }
}

.input-group-addon {
  display: inline-block;
  width: auto;
  height: 30px;
  min-width: 16px;
  padding: 4px 5px;
  font-size: 13px;
  font-weight: normal;
  line-height: 20px;
  text-align: center;
  text-shadow: 0 1px 0 #fff;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  background-color: #eee;
  border: 1px solid #ccc;
  border-left: 0;
  border-right: 0;
}
