$grid-gap: 16px;
$grid-gap-s: 12px;
$grid-gap-xs: 6px;

.columns {
  display: flex;
  flex-wrap: wrap;
  margin-left: -$grid-gap;
  margin-right: -$grid-gap;

  & + .columns {
    margin-top: $grid-gap;
  }

  &.-auto .column {
    flex: none;
  }

  &.-gap-s {
    margin-left: -$grid-gap-s;
    margin-right: -$grid-gap-s;

    & + .columns {
      margin-top: $grid-gap-s;
    }

    .column {
      padding-left: $grid-gap-s;
      padding-right: $grid-gap-s;
    }
  }

  &.-gap-xs {
    margin-left: -$grid-gap-xs;
    margin-right: -$grid-gap-xs;

    & + .columns {
      margin-top: $grid-gap-xs;
    }

    .column {
      padding-left: $grid-gap-xs;
      padding-right: $grid-gap-xs;
    }
  }
}

.column {
  flex: 1;
  max-width: 100%;
  padding-left: $grid-gap;
  padding-right: $grid-gap;

  &.-align-end {
    align-self: flex-end;
  }
}
